  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif !important;
}

.gradient-bg {
  background: linear-gradient(
    166deg,
    rgb(14 0 48) 0%,
    rgba(0, 0, 0, 1) 51%,
    rgb(30 1 95) 100%
  );
}
.gradient-bg-profile {
  background: linear-gradient(
    166deg,
    rgb(23 0 80) 0%,
    rgba(0, 0, 0, 1) 51%,
    rgb(51 35 86) 100%
  );
}

.gradient-bg-feeds-header {
  background: linear-gradient(
    115deg,
    rgb(23 3 69) 0%,
    rgba(0, 0, 0, 1) 51%,
    rgb(35 19 67) 100%
  );
}
.hx-layout {
  display: flex;
  z-index: 999999;
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
}

.hx-nav {
  position: absolute;
  padding-top: 10px;
  padding-bottom: 10px;
  top: 50px;
  width: 50px;
  background-color: black;
  left: -50px;
  height: 265px;
  display: flex !important;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  gap: 4px;
  box-shadow: black -3px 5px 30px -3px;
}

.hx-input {
  color: white;
  background: #1e293b;
  padding: 10px 22px;
  border-radius: 25px;
  border: 1px solid rgb(30 41 59 / 0.8);
  outline: none;
  width: 250px;
  cursor: pointer;
  font-size: 12px;
}

.hx-input::placeholder {
  font-size: 12px;
  color: #9ca3af;
}

.hx-link {
  color: #3b81f6;
  text-decoration: underline;
}

.hx-link:visited {
  color: #3b81f6;
}

.hx-link-white {
  color: #ffffff;
  text-decoration: underline;
}

.hx-link-white:visited {
  color: #ffffff;
}

.hx-button {
  background-color: #0064ff;
  color: white;
  border-radius: 25px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 20px;
  border: none;
  font-size: 14px;
  cursor: pointer;
}

.hx-button:hover {
  background: #0349b1;
}

.hx-button-disable {
  background: #0349b1;
  color: white;
  border-radius: 25px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 20px;
  border: none;
  font-size: 14px;
  cursor: not-allowed;
}

.hx-h1 {
  text-align: start !important;
  font-size: 24px !important;
  font-weight: 600 !important;
}
.hx-h2 {
  text-align: start !important;
  font-size: 20px !important;
  font-weight: 600;
}

.hx-h3 {
  text-align: start !important;
  font-size: 16px !important;
  font-weight: 400 !important;
}

.hx-p {
  font-size: 12px !important;
}

.mycenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bdr-l {
  border-left: 1px solid rgb(30 41 59 / 0.8);
}

.bdr-r {
  border-right: 1px solid rgb(30 41 59 / 0.8);
}

.bdr-t {
  border-top: 1px solid rgb(30 41 59 / 0.8);
}

.bdr-b {
  border-bottom: 1px solid rgb(30 41 59 / 0.8);
}

.bdr-all {
  border: 1px solid rgb(30 41 59 / 0.8);
}

.nav-btn-gradient {
  background-image: linear-gradient(
    208.15deg,
    #3466f6 -4.22%,
    #3466f6 -4.21%,
    #7c3aed 102.68%
  );
}

.nav-btn-gradient-hover:hover {
  background-image: linear-gradient(
    208.15deg,
    #3466f6 -4.22%,
    #3466f6 -4.21%,
    #7c3aed 102.68%
  );
}

.post-details h1 {
  font-size: 20px;
  margin: 15px 0px;
  font-weight: 600;
  color: #c8a4df;
}

.post-details h2 {
  font-size: 16px;
  font-weight: 600;
  color: #888de8;
  margin: 15px 0px;
}

.post-details h3 {
  font-size: 14px;
  margin: 15px 0px;
  font-weight: 600;
  color: #81bff7;
}

.post-details img {
  margin: 15px auto;
  padding: 5px;
  width: 100%;
  border-radius: 8px;
}

.post-details ul {
  margin: 5px;
  padding: 5px 15px;
}

.post-details ol {
  margin: 5px;
  padding: 5px 15px;
}

.post-details li {
  list-style: square;
  font-size: 12px;
}
.post-details p {
  color: white;
  margin: 10px 0px;
  font-size: 12px;
  line-height: 20px;
}

.post-details p code {
  color: #fe8d59;
  background: #fe8d594d;
  padding: 1px 5px;
  border-radius: 3px;
  margin: 0px 3px;
}

.post-details a {
  color: #3b81f6;
  text-decoration: underline;
  word-wrap: break-word;
  display: block;
  width: 100%;
}

.post-details a:visited {
  color: #3b81f6;
}
.post-details em {
  color: gray;
}

.post-details iframe {
  width: 100%;
  height: 185px;
}

.post-details pre {
  border: none;
  box-shadow: 2px 2px 8px rgb(40, 40, 40);
  border-radius: 5px;
  padding: 20px;
  margin: 20px 0px;
  color: #fafafa;
  background-color: #1e293b;
  overflow-x: auto;
  font-size: 12px;
}

.post-details pre .hljs-keyword {
  color: #c586c0;
}
.post-details pre .hljs-string {
  color: #ce9178;
}
.post-details pre .hljs-function {
  color: #dcdcaa;
  font-weight: bold;
}
.post-details pre .hljs-params {
  color: inherit;
}
.post-details pre .hljs-built_in {
  color: #9cdcfe;
}
.post-details pre .hljs-number {
  color: #b5cea8;
}
.post-details pre .hljs-tag {
  color: #569cd6;
}
.post-details pre .hljs-name {
  color: #9cdcfe;
}
.post-details pre .hljs-attr {
  color: #d7ba7d;
}
.post-details pre .xml .hljs-tag {
  color: #569cd6;
}

.post-details pre .hljs-comment {
  color: #469972;
}

.post-details table {
  border: 1px solid rgb(30 41 59 / 0.8);
}

.post-details table td {
  border: 1px solid rgb(30 41 59 / 0.8);
  padding: 10px;
  font-size: 12px;
}

.post-details blockquote {
  border-left: 5px solid #354c6f;
  padding-left: 12px;
  margin: 20px 0px;
  border-radius: 3px;
}

.feeds-tab {
  display: flex;
  justify-content: center;
  font-size: 12px;
  align-items: center;
  gap: 3px;
  border-radius: 30px;
  padding: 10px;
  cursor: pointer;
}

.feeds-tab:hover {
  background-color: #17233f;
}

.feeds-tab-active {
  cursor: pointer;
  display: flex;
  justify-content: center;
  font-size: 12px;
  align-items: center;
  gap: 3px;
  border-radius: 30px;
  padding: 10px;
  background-color: #17233f;
}
